
.topbar-root[data-v-323e02b8] {
  position: fixed;
  top: 0;
  left: 0;
  width: 55px;
  height: 55px;
/*background rgba(0,0,0,0.2)*/
  color: rgba(255,255,255,0.5);
  z-index: 20;
/*.open
    .menu
      filter blur(0)
      transform scale(1)*/
}
.topbar-root .menu-btn-box[data-v-323e02b8] {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 30px;
  height: 30px;
  z-index: 10;
}
.topbar-root .menu-container[data-v-323e02b8] {
  position: fixed;
  left: -200px;
  top: 0;
  width: 500px;
  padding-left: 200px;
  padding-top: 80px;
  height: 100%;
  box-sizing: border-box;
/*background #323232*/
  background: rgba(50,50,50,0.9);
  box-shadow: 5px 0 8px 2px rgba(0,0,0,0.1);
  overflow: hidden;
/*filter blur(10px)*/
}
.topbar-root .menus[data-v-323e02b8] {
  width: 300px;
  height: 100%;
  overflow: auto;
  color: rgba(255,255,255,0.8);
  overflow: hidden;
