  line-height: 60px;
  font-size: 18px;
  text-align: left;
  text-shadow: 2px 2px 1px rgba(0,0,0,0.3);
  transition: all ease 0.3s;
  overflow: hidden;
/*filter blur(3px)
      transform scale(1.2, 0.7)*/
}
.topbar-root .menus .menu[data-v-323e02b8]:hover {
/*background rgba(255,255,255,0.1)*/
  color: rgba(255,255,255,0.9);
  padding-left: 53px;
}
