  font-size: 25px;
  margin-right: 15px;
}
.menu-container-transition-enter-active[data-v-323e02b8] {
  -webkit-animation: menu-container-transition-in 0.5s;
          animation: menu-container-transition-in 0.5s;
}
.menu-container-transition-leave-active[data-v-323e02b8] {
  -webkit-animation: menu-container-transition-out 0.5s;
          animation: menu-container-transition-out 0.5s;
}
.menu-transition-enter-active[data-v-323e02b8] {
  -webkit-animation: menu-transition-in 0.4s;
          animation: menu-transition-in 0.4s;
}
/*.menu-transition-leave-active
  filter url("#filter-blur-x")*/
.menu-transition-move[data-v-323e02b8] {
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
/*.filter-x.menu-transition-move
  filter url("#filter-blur-x")
.filter-y.menu-transition-move
  filter url("#filter-blur-y")*/
/*@keyframes menu-transition-out
  0%
    transform translateX(0)
  100%
    transform translateX(-500px)*/
@-webkit-keyframes menu-container-transition-in {
0% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
}
60% {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
}
100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes menu-container-transition-in {
0% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
}
60% {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
}
100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes menu-container-transition-out {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
}
}
@keyframes menu-container-transition-out {
0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
100% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
}
}
@-webkit-keyframes menu-transition-in {
0% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
}
60% {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
}
100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes menu-transition-in {
0% {
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
}
60% {
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
}
100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}


