
.home-root {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 15vh 10vw;
  box-sizing: border-box;
  color: rgba(255,255,255,0.5);
}
.home-root .content-container {
  width: 100%;
  height: 100%;
  position: relative;
}
.home-root .text-box {
  text-align: center;
}
.home-root .text-box .name {
  display: block;
  margin-top: 20px;
  font-size: 18px;
  text-shadow: 2px 2px 2px rgba(0,0,0,0.5);
  font-weight: 200;
}
.home-root .text-box .description {
  font-size: 12px;
  text-shadow: 2px 2px 2px rgba(0,0,0,0.5);
}
.home-root .text-box .description .work-time-number {
  display: inline;
  font-size: 15px;
}
.home-root .text-box .description .link {
  margin-top: 10px;
  padding: 5px 20px;
  border-radius: 3px;
  font-size: 15px;
  opacity: 0.5;
  transition: all ease 0.3s;
  outline: none;
}
.home-root .text-box .description .link:hover {
  opacity: 0.7;
}


